footer{
    padding-bottom: 59px;
    .container::before {
        content: "";
        display: inline-block;
        width: 100%;
        height: 2px;
        margin-bottom: 29px;
        padding: 0 15px;
        background-color: $color-primary;
    }
    .footer-logo {
        margin-bottom: 30px;

    .row {
            flex-wrap: nowrap;
            justify-content: space-between;
        } 
    }
    .footer-list{
        max-width: 300px;
        list-style-type: none;
        margin-top: 5px;
        display: flex;
        flex-direction: column;
        
    }
}

